<script>
export default {
  name: "Sounds",
  playSound: function (src) {
    new Audio("sounds/" + src).play().then();
  },
  playError: function () {
    this.playSound("error1.mp3");
  },
  playSuccess: function () {
    this.playSound("success1.mp3");
  },
  playBigSuccess: function () {
    this.playSound("big_success1.mp3");
  },
}
</script>
